.text-block {
  padding: 5%;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.text-block--half {
  width: 50%;
}

.text-block--half .text-block__title,
.text-block--half .text-block__text,
.text-block--half .text-block__subtext,
.text-block--half .text-block__copyright {
  max-width: 600px;
}

.text-block__title {
  font-size: 34px;
  line-height: 1.18;
  font-weight: 800;
  max-width: 950px;
}

.text-block__text {
  font-size: 24px;
  font-weight: 300;
  line-height: 1.42;
  margin-bottom: 15px;
  max-width: 950px;
}

.text-block__subtext {
  font-size: 16px;
  line-height: 1.88;
  font-weight: 300;
  max-width: 950px;
}

.text-block__copyright {
  font-size: 12px;
  font-weight: 300;
  line-height: 2.5;
  max-width: 950px;
}
